body {
	/* font-family: Lato, Sofia Pro, Helvetica Neue, sans-serif; */
	background-color: #ddd;
	background-image: url('../img/bg2.png');
	font-family: 'Tajawal', sans-serif;
  line-height: 1.4em;
	margin: 0;
	padding: 60px 20px;
}

.chrome-container {
	background-color: #fff;
	max-width: 800px;
	margin: 0 auto;
	position: relative;
}

h1 {
	font-size: 48pt;
	font-weight: 900;
	margin: 30px 0 0 -5px;
}

h4 {
	font-size: 14pt;
	margin-top: 30px;
}

h5 {
	margin: 15px 0 4px !important;
}

.heading-column h5 {
	font-weight: 200;
	letter-spacing: 0.3em;
	margin: 0 0 8px;
}

p {
	padding-top: 0;
}

a {
	color: darkslateblue;
}

a.nostyle {
	color: #000;
	text-decoration: none;
}

em {
	background: #f9f9f9;
	font-weight: 300;
	font-style: normal;
}

.bigger {
	font-size: 1.4em;
	line-height: 1em;
}

.smaller {
	font-size: 0.9em;
}

.text-right {
	text-align: right;
}

.float-right {
	float: right;
}

.float-left {
	float: left;
}

ul.unstyled, ol.unstyled {
	list-style-type: none;
	margin-left: 0;
	padding-left: 0;
}

table.unstyled td {
	padding: 0;
}

.taglist {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.taglist li {
	background: #e5e5e5;
	border-radius: 3px;
	display: inline-block;
	font-size: 0.85em;
	margin: 0 10px 10px 0;
	padding: 0 !important;
}

.taglist li span {
	display: inline-block;
}

.taglist li .text {
	font-weight: 500;
	padding: 5px 8px;
}

.taglist li .badge {
	background: #f5f5f5;
	border-radius: 3px;
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
	border-left: 1px solid #fff;
	color: #000;
	font-weight: 200;
	padding: 8px;
}

/* SECTIONS */

.section {
	/* border-top: 1px solid #aaa; */
	margin-top: 40px;
  padding: 20px 80px;
}

.section h3 {
	font-family: 'Indie Flower', serif;
	margin: 0 0 15px;
	font-size: 22pt;
	font-weight: 500;
}

/* TITLE BAR */

.title-bar {
	padding: 12px 20px;
}

/* HEADER */

header {
	background-color: #111;
	border: none;
	color: #fff;
	margin: 0 0 100px;
  padding: 10px 20px 40px;
	position: relative;
	text-align: center;
}

.header-image-container {
	height: 100%;
	left: 0;
	overflow: hidden;
	position: absolute;
	top: 0;
	width: 100%;
}

.header-image-container img {
	margin-top: -19%;
	opacity: 0.35;
	min-width: 100%;
}

.header-content {
	position: relative;
}

.me {
	margin-bottom: -100px;
	z-index: 2;
}

.me img {
  border: 7px solid #fff;
  border-radius: 120px;
  /* box-shadow: 2px 2px 15px -5px rgba(0, 0, 0, 0.75); */
  height: 220px;
  width: 220px;
}

/* header.section::after {
	background-color: transparent;
	background-position: 50% 25%;
	background-repeat: no-repeat;
	background-size: cover;
	opacity: 0.7;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
} */

#objective {
  margin-top: 0;
}

/* ITEMS */

.greeting {
	font-size: 1.6em;
}

.item {
	border-bottom: 1px dotted #ddd;
	font-size: 0.9em;
	padding-bottom: 10px;
	margin-bottom: 20px;
}

.item .title {
	font-size: 1.4em;
	font-weight: bold;
	margin-bottom: 6px;
}

.item:first-child {
	font-size: 1.2em;
}

.item:first-child .description p {
	font-size: 0.8em;
}

.item:first-child .description p:first-child {
	font-size: 1em;
}

.item:last-child {
	border-bottom: none;
}

@media (max-width: 600px) {
	h1 {
		font-size: 36pt;
	}

	h4 {
		font-size: 11pt;
		margin-top: 15px;
	}
}

@media print {
  body {
    font-size: 0.8em;
  }

	html,
	body,
	header,
	h1,
	.container {
		margin-top: 0;
		padding-top: 0;
	}

	body {
		font-size: 14pt;
		padding: 0 40px;
	}

	h5 {
		font-size: 1.1em;
	}

	.col-md-9,
	.col-md-3 {
	    display: block;
	}

	.section h3 {
		font-size: 18pt;
	}

	.bigger {
		font-size: 1.5em;
		line-height: 1em;
	}

	.smaller {
		font-size: 1em;
	}

	.taglist li {
		font-size: 1.1em;
		margin-right: 5px;
	}

	.taglist li:after {
		content: "|";
		color: #777;
		padding-left: 9px;
	}

	.taglist li:last-child:after {
		content: "";
		padding-left: 0;
	}

	.taglist li .text {
		padding-right: 0;
	}

	.taglist li .badge {
		font-size: 1em;
		padding-left: 2px;
	}

	.taglist li .badge:before {
		content: "(";
	}

	.taglist li .badge:after {
		content: ")";
	}

  a:after {
    color: #777;
    content: " [" attr(href) "] ";
    font-size: 0.8em;
    font-weight: normal;
  }
}



